/* Extra small devices (phones, 600px and down) */
// @media only screen and (max-width: 320px)
$xsmall: 320px;
/* Small devices (portrait tablets and large phones, 600px and up) */
// @media only screen and (min-width: 481px)
$small: 481px;
/* Medium devices (landscape tablets, 769px and up) */
// @media only screen and (min-width: 769px)
$medium: 769px;
/* Large devices (laptops/desktops, 1025px and up) */
// @media only screen and (min-width: 1025px)
$large: 1025px;
/* Extra large devices (large laptops and desktops, 1201px and up) */
// @media only screen and (min-width: 1210px)
$xlarge: 1201px;

/* FONTS */
$font1: "Amiri",
serif;
$font2: "Mirza",
cursive;
$font3: "Scheherazade",
serif;
$font4: 'Mochiy Pop P One',
sans-serif;
$font_arabic: 'Lateef',
cursive;
$font_arabic02: 'Aref Ruqaa',
serif;

/* SCSS HEX */
$main-color: #3d425c;
$secondary-color: #566092;
$third-color: #ff787e;
$white-color: #ffffffff;
$black-color: #000000ff;
$red-color: #ff0000ff;
$green-color: #2aa70b;
$blue-color: #366feb;
$yellow-color: #e9b93f;
$offer-color: #0f04;

/* SCSS Gradient */
$gradient-top: linear-gradient(0deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-right: linear-gradient(90deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-bottom: linear-gradient(180deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-left: linear-gradient(270deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-top-right: linear-gradient(45deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-bottom-right: linear-gradient(135deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-top-left: linear-gradient(225deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-bottom-left: linear-gradient(315deg,
  $main-color,
  $secondary-color,
  $third-color);
$gradient-radial: radial-gradient($main-color, $secondary-color, $third-color);